import { Container, Grid, makeStyles, Typography } from '@material-ui/core';
import React, { useState } from 'react';
import { animated, useSpring } from 'react-spring';
import FadeUp from '../Animated/FadeUp';
import BenefitCard from '../Cards/BenefitCard';
import { ImpressionArea } from '@lubycon/react';

const activities = [
  {
    title: ' ',
    description: `            .\n      "           ".`,
  },
  {
    title: ' ',
    description: `          .\n\n           .`,
  },
  {
    title: ' ',
    description: `               .\n         .`,
  },
  {
    title: ' ',
    description:
      '          UI Kit         .',
  },
];

const useStyles = makeStyles(({ palette, spacing, typography, breakpoints }) => ({
  root: {
    padding: `${spacing(15)}px 0`,
    backgroundColor: palette.grey['100'],
    [breakpoints.down('md')]: {
      padding: `${spacing(10)}px 0`,
    },
  },
  title: {
    fontWeight: typography.fontWeightBold,
    [breakpoints.down('md')]: {
      fontSize: typography.h6.fontSize,
    },
  },
  description: {
    marginBottom: spacing(5),
  },
}));

const ActivitySection = () => {
  const [visible, setVisible] = useState(false);
  const styles = useStyles();

  const titleAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
  });
  const descriptionAnimation = useSpring({
    opacity: visible ? 1 : 0,
    transform: visible ? 'translateX(0)' : 'translateX(-50px)',
    delay: 300,
  });

  return (
    <section className={styles.root}>
      <Container>
        <ImpressionArea onImpressionStart={() => setVisible(true)}>
          <animated.div style={titleAnimation}>
            <Typography variant="h4" component="h2" gutterBottom={true} className={styles.title}>
                
            </Typography>
          </animated.div>
          <animated.div style={descriptionAnimation}>
            <Typography className={styles.description}>
                 <strong> </strong>.      
                 .
              <br />
            </Typography>
          </animated.div>
          <Grid container spacing={2}>
            {activities.map(({ title, description }, index) => (
              <Grid key={index} item xs={12} sm={3}>
                <FadeUp>
                  <BenefitCard title={title} description={description} />
                </FadeUp>
              </Grid>
            ))}
          </Grid>
        </ImpressionArea>
      </Container>
    </section>
  );
};

export default ActivitySection;
